<template>
  <link rel="stylesheet" href="./css/pico.min.css">
  <main class="container">
    <img alt="Vue logo" src="./assets/logo.png">
    <HelloWorld msg="Counter App"/>
      <div id="app" v-for="cntr in cntrs" :key="cntr.id">
        <div class="grid">
          <div>
            <button :id="'plus_' + cntr.id + '_10'" @click.prevent="countIt($event)">+ 10 {{ cntr.id }}</button>
          </div>
          <div>
            <button :id="'plus_' + cntr.id + '_7'" @click.prevent="countIt($event)">+ 7 {{ cntr.id }}</button>
          </div>
          <div>
            <button :id="'plus_' + cntr.id + '_4'" @click.prevent="countIt($event)">+ 4 {{ cntr.id }}</button>
          </div>
          <div>
            <button :id="'plus_' + cntr.id + '_2'" @click.prevent="countIt($event)">+ 2 {{ cntr.id }}</button>
          </div>
          <div>
            <button :id="'plus_' + cntr.id + '_1'" @click.prevent="countIt($event)">+ 1 {{ cntr.id }}</button>
          </div>
        </div>
        <br>
        <div style="display:none">
          <button :id="'minus_' + cntr.id + '_10'" @click.prevent="countIt($event)">- 10 {{ cntr.id }}</button>
          <button :id="'minus_' + cntr.id + '_7'" @click.prevent="countIt($event)">- 7 {{ cntr.id }}</button>
          <button :id="'minus_' + cntr.id + '_4'" @click.prevent="countIt($event)">- 4 {{ cntr.id }}</button>
          <button :id="'minus_' + cntr.id + '_2'" @click.prevent="countIt($event)">- 2 {{ cntr.id }}</button>
          <button :id="'minus_' + cntr.id + '_1'" @click.prevent="countIt($event)">- 1 {{ cntr.id }}</button>
        </div>
        <span id="{{ cntr.id }}">{{ cntr.id }} count: <span id="{{ cntr.id }}_count">{{ cntr.amt }}</span></span>
      </div>
    <br><br>
    <br><br>
    <div v-for="(history, index) in histories" :key="index">
      {{ history.txt }}<br>
    </div>
  </main>
</template>

<script>
import HelloWorld from './components/HelloWorld.vue'

export default {
  name: 'App',
  components: {
    HelloWorld
  },
  data () {
    return {
      histories: [
        {txt: '1/31/2023 rc 5 other 20 torn 4 kab 0'},
        {txt: '1/30/2023 rc 3 other 33 torn 0 kab 1'}
      ],
      counters: ['rc', 'other', 'torn', 'kab'],
      cntrs: [{id: 'rc', amt: 0}, {id: 'other', amt: 0}, {id: 'torn', amt: 0}, {id: 'kab', amt: 0}],
      exportResults: '',
      rc: 0,
      other: 0,
      torn: 0,
      kab: 0
    }
  },
  mounted () {
    this.process_counts()
    this.create_history()
  },
  methods: {

    process_counts () {
      this.rc = 0
      this.other = 0
      this.torn = 0
      this.kab = 0
      this.cntrs = [{id: 'rc', amt: 0}, {id: 'other', amt: 0}, {id: 'torn', amt: 0}, {id: 'kab', amt: 0}]
      const today = new Date().toLocaleDateString()
      for (const key in localStorage) {
        for (const x in this.counters) {
          const counter = this.counters[x]
          if (key.includes(counter) && !key.includes('history_')) {
            const keyBits = key.split('-')
            const dt = keyBits[1]
            const amountToAdd = parseInt(localStorage.getItem(key))
            if (dt.includes(today)) {
              for (const y in this.cntrs) {
                if (this.cntrs[y].id === counter) {
                  this.cntrs[y].amt += amountToAdd
                }
              }
            } else {
              const historyKey = 'history_' + keyBits[0] + '-' + dt
              if (localStorage[historyKey] === undefined) {
                localStorage.setItem(historyKey, 0)
              }
              const thereBefore = parseInt(localStorage.getItem(historyKey))
              const newAmount = thereBefore + amountToAdd
              localStorage.setItem(historyKey, newAmount)
            }
          }
        }
      }
    },

    create_history() {
      var creatingHistory = []
      for (const key in localStorage) {
        const historyKey = 'history_'
        if (key.includes(historyKey)) {
          var hasDayForHistorySet = 0
          const lastBits = key.split('-')
          const whichDay = lastBits[1].split(',')
          const justTheDate = whichDay[0]
          const firstBits = lastBits[0].split('_')
          const typ = firstBits[1]
          for (const x in this.histories) {
            if (this.histories[x].txt.includes(justTheDate)) {
              hasDayForHistorySet = 1
            }
          }
          if (hasDayForHistorySet == 0 && creatingHistory[justTheDate] === undefined) {
            creatingHistory[justTheDate] = {}
          }
          for (const x in this.counters) {
            const counter = this.counters[x]
            if (creatingHistory[justTheDate][counter] === undefined) {
              creatingHistory[justTheDate][counter] = 0
            }
            if (typ === counter) {
              creatingHistory[justTheDate][counter] += parseInt(localStorage.getItem(key))
            }
          }
        }
      }
      for (const dt in creatingHistory) {
        var text = {txt: dt}
        const bits = creatingHistory[dt]
        for (const x in bits) {
          text.txt = text.txt + ' ' + x + ' ' + bits[x]
        }
        this.histories.push(text)
      }
    },

    export () {
      this.exportResults = '['
      for (const key in localStorage) {
        this.exportResults = this.exportResults + '{"' + key + '":' + localStorage.getItem(key) + '},'
      }
      this.exportResults = this.exportResults + ']'

    },

    countIt (event) {
      const data = event.target.id.split('_')
      const typ = data[0]
      const which = data[1]
      const amount = data[2]
      const now = new Date().toLocaleString([], {hour12: false})
      const key = which + '-' + now
      let toSaveDown = 0
      if (typ === 'plus') {
        toSaveDown += amount
      } else if (typ === 'minus') {
        toSaveDown -= amount
      }
      localStorage.setItem(key, toSaveDown)
      this.process_counts()
    }

  }
}
</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

</style>
